import { Component } from "react";
import Navitem from "./Navitem";
import profilepic from "../img/me.jpg";
import { Link } from "react-scroll";

class Navbar extends Component {
  constructor(props) {
    super(props);
    this.state = {
      NavItemActive: "",
    };
  }
  activeitem = (x) => {
    if (this.state.NavItemActive.length > 0) {
      document
        .getElementById(this.state.NavItemActive)
        .classList.remove("active");
    }
    this.setState({ NavItemActive: x }, () => {
      document.getElementById(this.state.NavItemActive).classList.add("active");
    });
  };
  render() {
    return (
      <nav>
        <div className="neu2">
          <div className="neu1">
            <img
              src={profilepic}
              className="profilepic"
              alt="Profile Pic"
            ></img>
          </div>
        </div>
        <section className="info">
          <h2>
            Amir Hossein <mark>Ebrahimi</mark>
          </h2>
          <p>Front-End Developer</p>
        </section>
        <ul>
          <Link
            activeClass="active"
            to="Home"
            spy={true}
            smooth={true}
            offset={240}
            duration={500}
          >
            <Navitem item="Home" tolink="/" activec={this.activeitem}></Navitem>
          </Link>
          <Link
            activeClass="active"
            to="About"
            spy={true}
            smooth={true}
            offset={200}
            duration={500}
          >
            <Navitem
              item="About"
              tolink="/about"
              activec={this.activeitem}
            ></Navitem>
          </Link>
          <Link
            activeClass="active"
            to="Portfolio"
            spy={true}
            smooth={true}
            offset={140}
            duration={500}
          >
            <Navitem
              item="Portfolio"
              tolink="/portfolio"
              activec={this.activeitem}
            ></Navitem>
          </Link>
          <Link
            activeClass="active"
            to="Contact"
            spy={true}
            smooth={true}
            offset={90}
            duration={500}
          >
            <Navitem
              item="Contact"
              tolink="/contact"
              activec={this.activeitem}
            ></Navitem>
          </Link>
        </ul>
      </nav>
    );
  }
}

export default Navbar;
